import axios from 'axios'

import { useState, useEffect } from 'react'
import { useTheme } from '@mui/material/styles'

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import ReactMarkdown from 'react-markdown'
import rehypeRaw from 'rehype-raw'
import rehypeRemark from 'rehype-remark'
import remarkRehype from 'remark-rehype'


import {Markup} from 'interweave'

interface DescriptionProps {

}
export const Description = (props: DescriptionProps) => {

    const url = 'https://raw.githubusercontent.com/michaelckearney/babble/main/README.md'
    const [read, setRead] = useState("")

    var config = {
        headers: {
            'Cache-Control': 'no-cache'
        }
    }

    useEffect(() => {
        axios.get(url).then((response) => {
            setRead(response.data);
        });
    }, []);

    const theme = useTheme()

    return (
        <Box position="absolute" top="0" bottom="0" left="1em" right="1em" sx={{overflowY: "scroll"}}>
        <Typography color="white">
            {/* <Markup content={read} /> */}
            <ReactMarkdown
                children={read}
                allowElement={() => {return true}} rehypePlugins={[rehypeRaw]}  />
        </Typography>
        </Box>
    )
}
export default Description